import { SelectionModel } from "@angular/cdk/collections";
import { Component, OnInit, ViewChild } from "@angular/core";
import { MatDialog, MatDialogConfig } from "@angular/material/dialog";
import { MatMenuTrigger } from "@angular/material/menu";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Router } from "@angular/router";
import { Subscription, Subject, takeUntil } from "rxjs";
import { BudgetsService } from "src/app/erp-budget/data/services/budget.service";
import { AccessControlService } from "src/app/erp-procurement/data/services/_AccessControlService.service";

import { SnackbarService } from "src/app/shared/services/snackbar.service";
import Swal from "sweetalert2";
import { BudgetValidationComponent } from "../../dialogs/budget-validation/budget-validation.component";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MngBudgetDialogComponent } from "../../budget-imports/mng-budget-dialog/mng-budget-dialog.component";

@Component({
  selector: "app-all-budgets",
  templateUrl: "./all-budgets.component.html",
  styleUrls: ["./all-budgets.component.scss"],
})
export class AllBudgetsComponent implements OnInit {
  displayedColumns: string[] = [
    "select",
    "id",
    "budgetCode",
    "expenseCategory",
    "expenseCode",
    "expenseName",
    "expenseBudgetAmount",
    "status",
    "action",
  ];


  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  contextMenu: MatMenuTrigger;
  contextMenuPosition = { x: "0px", y: "0px" };
  subscription!: Subscription;
  selection = new SelectionModel<any>(true, []);
  data: any;
  error: any;
  formData: any;
  isLoading = true;
  pageFunction = "Update";
  destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  Form: FormGroup;
  currentYear: number;
  constructor(
    private router: Router,
    private snackbar: SnackbarService,
    private budgetsService: BudgetsService,
    private accessControlService: AccessControlService,
    private dialog: MatDialog,
    private fb: FormBuilder
  ) {
    this.currentYear = new Date().getFullYear();
  }

  ngOnInit(): void {
    this.Form = this.fb.group({
      year: [this.currentYear, Validators.required],
    });

    this.getData();
  }
  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  // ****************************************************************************************************************************

  budgets = [];
  getData() {
    this.isLoading = true;

    let params = { year: this.Form.value.year };
    this.budgetsService
      .getBudgetsByYear(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("this.data :", res);
          if (res.statusCode === 302) {
            this.data = res;
            this.budgets = this.data.entity;
            this.isLoading = false;

            console.log("this.data :", this.data);
          } else {
            this.snackbar.showNotification("snackbar-danger", res.message);
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => {            this.isLoading = false;
        },
      }),
      Subscription;
  }

  refresh() {
    this.getData();
  }
  refreshData() {
    this.getData();
  }
  // ****************************************************************************************************************************

  addBudget() {
    this.router.navigate(["/erp-budget/budget/manage-budgets"]);
  }

  editBudget(data: any) {
    let route = "/erp-budget/budget/manage-budgets";
    this.router.navigate([route], {
      queryParams: { id: data.id, action: "Update" },
    });
  }
  viewBudget(data: any) {
    let route = "/erp-budget/budget/manage-budgets";
    this.router.navigate([route], {
      queryParams: { id: data.id, action: "View" },
    });
  }

  //verify Budget
  validateBudget(data: any) {
    let route = "/erp-budget/budget/manage-budgets";
    this.router.navigate([route], {
      queryParams: {
        id: data.id,
        action: "Verify",
      },
    });
  }

  deleteBudget(id: number) {
    Swal.fire({
      title: "Are you sure?",
      text: "Do you really want to delete this Budget?",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, Delete Budget!",
    }).then((result) => {
      if (result.isConfirmed) {
        let params = { id: id };

        this.budgetsService
          .deleteBudgetTemporarily(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              this.snackbar.showNotification("snackbar-success", res.message);
            },
            error: (err) => {
              this.snackbar.showNotification("snackbar-danger", err.message);
            },
            complete: () => {
              this.getData();
            },
          }),
          Subscription;
      }
    });
  }

  // ****************************************************************************************************************************
  //*************************************************************************************************************** */
}
