import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  ViewChild,
} from "@angular/core";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { MatDialog, MatDialogConfig } from "@angular/material/dialog";
import { AllBudgetsComponent } from "../../budget/all-budgets/all-budgets.component";
import { request } from "http";
import { send } from "process";
import { MngBudgetDialogComponent } from "../mng-budget-dialog/mng-budget-dialog.component";

@Component({
  selector: "app-budget",
  templateUrl: "./budget.component.html",
  styleUrls: ["./budget.component.scss"],
})
export class BudgetComponent implements OnInit {
  @Input() budgets: any[];
  @Output() refreshData: EventEmitter<any> = new EventEmitter<any>();

  displayedColumns: string[] = ["name", "budgetexps"];
  dataSource: MatTableDataSource<any>;

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;

  months: string[] = [
    "Jan",
    "Feb",
    "Mar",
    "Apr",
    "May",
    "Jun",
    "Jul",
    "Aug",
    "Sep",
    "Oct",
    "Nov",
    "Dec",
  ];

  constructor(private dialog: MatDialog) {}

  ngOnInit(): void {
    console.log("budgets: ", this.budgets);
  }

  ngAfterViewInit() {
    this.dataSource = new MatTableDataSource(this.budgets);
    this.dataSource.sort = this.sort;
    this.dataSource.paginator = this.paginator;
  }

  getAmountForMonth(budgetItem: any, month: string): number {
    if (budgetItem && budgetItem.budgetdistributions) {
      const budgetDistribution = budgetItem.budgetdistributions.find(
        (distribution: any) => distribution.month === month
      );
      return budgetDistribution
        ? parseFloat(budgetDistribution.amount.toFixed(4))
        : 0;
    } else {
      return 0;
    }
  }

  calculateTotal(budgetexps: any[], index: number, month: string): number {
    let total = 0;
    for (let i = 0; i <= index; i++) {
      total += this.getAmountForMonth(budgetexps[i], month);
    }
    return total;
  }

  calculateCategoryTotal(budget: any, month: string): number {
    let total = 0;
    if (budget && budget.budgetexps) {
      for (const budgetexp of budget.budgetexps) {
        const budgetDistribution = budgetexp.budgetdistributions.find(
          (distribution: any) => distribution.month === month
        );
        if (budgetDistribution) {
          total += budgetDistribution.amount;
        }
      }
    }
    return total;
  }
  calculateGrandTotal(month: string): number {
    let grandTotal = 0;
    this.budgets.forEach((budget) => {
      budget.budgetexps.forEach((budgetexp) => {
        budgetexp.budgetdistributions.forEach((distribution) => {
          if (distribution.month === month) {
            grandTotal += distribution.amount;
          }
        });
      });
    });
    return grandTotal;
  }

  getYears(): number[] {
    const years: number[] = [];
    if (this.budgets && this.budgets.length > 0) {
      this.budgets[0].budgetexps[0].budgetdistributions.forEach(
        (distribution) => {
          years.push(distribution.year);
        }
      );
    }
    return years;
  }

  //
  getDates(): any[] {
    const dates: any[] = [];
    if (this.budgets && this.budgets.length > 0) {
      this.budgets[0].budgetexps[0].budgetdistributions.forEach(
        (distribution) => {
          dates.push(distribution.date);
        }
      );
    }
    return dates;
  }
  mngBudgetRecord(selectedRecord: any) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "700px";
    dialogConfig.data = {
      action: "Edit Budget",
      selected: selectedRecord,
    };
    const dialogRef = this.dialog.open(MngBudgetDialogComponent, dialogConfig);
    dialogRef.afterClosed().subscribe((result) => {
      console.log("result:::", result);
      this.refreshData.emit();
    });
  }
}
