import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RoutePrivilegeGuard } from 'src/app/erp-procurement/data/services/_AccessControlAuthGuard.service';
import { AllBudgetsComponent } from './pages/budget/all-budgets/all-budgets.component';
import { ManageBudgetsComponent } from './pages/budget/manage-budgets/manage-budgets.component';
import { BudgetReportsComponent } from './pages/budget-reports/budget-reports.component';

const routes: Routes = [
   //Budgets
   {
    path: "all-budgets",
    component: AllBudgetsComponent,
    canActivate: [RoutePrivilegeGuard],
   data: {  clientName: 'BudgetModule', requiredPrivilege: ["All Budgets"] },
  },
  {
    path: "manage-budgets",
    component: ManageBudgetsComponent,
    canActivate: [RoutePrivilegeGuard],
   data: {  clientName: 'BudgetModule', requiredPrivilege: ["Manage Budgets"] },
  },
  {
    path: "budget-reports",
    component: BudgetReportsComponent,
    canActivate: [RoutePrivilegeGuard],
   data: {  clientName: 'BudgetModule', requiredPrivilege: ["Dashboard"] },
  },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BudgetRoutingModule { }
