import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable, map } from "rxjs";
import { environment } from "src/environments/environment.prod";

@Injectable({
  providedIn: "root",
})

export class BudgetReportsService {
  constructor(private http: HttpClient) { }

  // Download variance Excel report
  downloadVarianceExcelReport(params: any): Observable<any> {
    const headers = new HttpHeaders().set('Accept', 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet');
    const requestOptions: any = {
      params: params,
      headers: headers,
      responseType: 'arraybuffer', 
      withCredentials: false,
    };
    const API_URL = `${environment.baseUrlFinance}/api/v1/report/download/budget/variance`;
    return this.http.get(API_URL, requestOptions).pipe(
      map((response: ArrayBuffer) => { 
        return {
          filename: 'Variance_Report.xlsx',
          data: new Blob([response], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' }),
        };
      })
    );
  }

  // Download budget balance Excel report
  downloadBudgetBalanceReport(params: any): Observable<any> {
    const headers = new HttpHeaders().set('Accept', 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet');
    const requestOptions: any = {
      params: params,
      headers: headers,
      responseType: 'arraybuffer', 
      withCredentials: false,
    };
    const API_URL = `${environment.baseUrlFinance}/api/v1/report/download/budget/balance`;
    return this.http.get(API_URL, requestOptions).pipe(
      map((response: ArrayBuffer) => { 
        return {
          filename: 'Budget_Balance_Report.xlsx',
          data: new Blob([response], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' }),
        };
      })
    );
  }
}
