  collectErrors(control: AbstractControl, path: string = "") {
    if (control instanceof FormGroup) {
      Object.keys(control.controls).forEach((key) => {
        const subControl = control.get(key);
        const subPath = this.getErrorPath(path, key);
        this.collectErrors(subControl, subPath);
      });
    } else {
      const errors = control.errors;
      if (errors) {
        const itemError = {
          field: path,
          message: this.getErrorMessage(errors),
        };
        this.itemErrors.push(itemError);
      }
    }
  }

  getErrorPath(path: string, key: string): string {
    return path ? `${path}.${key}` : key;
  }

  getErrorMessage(errors: any): string {
    if (errors.required) {
      return "This field is required.";
    } else if (errors.pattern) {
      return "Invalid value.";
    }
    return "";
  }

  private readonly templateFilename = "BudgetTemplate.xlsx";

  downloadTemplate() {
    const link = document.createElement("a");
    link.setAttribute("type", "hidden");
    link.href = `/assets/templates/${this.templateFilename}`;
    link.download = this.templateFilename;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }
  // ***************************************************************************************************************************
  // Validation

  hasAccess: boolean;
  onValidate(userType) {
    let privilege = "";

    switch (userType) {
      case "HOD":
        privilege = "HOD Budget Validation";
        break;
      case "Finance":
        privilege = "Finance Budget Validation";
        break;
      default:
        return;
    }

    this.hasAccess = this.accessControlService.hasPrivilege("BudgetModule", [
      privilege,
    ]);

    if (this.hasAccess) {
      const dialogConfig = new MatDialogConfig();
      dialogConfig.disableClose = false;
      dialogConfig.autoFocus = true;
      dialogConfig.width = "800px";
      dialogConfig.data = {
        data: this.formData,
        userType: userType,
      };

      const dialogRef = this.dialog.open(
        BudgetValidationComponent,
        dialogConfig
      );

      dialogRef.afterClosed().subscribe((result) => {
        this.router.navigate(["/erp-budget/budget/all-budgets"]);
      });
    }
  }

  // ****************************************************************************************************************************
  // Loook Ups
  selectedCostCenterItem: any[] = [];
  costCenterLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "600px";
    dialogConfig.data = {
      action: "View CostCenters",
      selected: this.selectedCostCenterItem,
    };
    const dialogRef = this.dialog.open(CcentreLookupComponent, dialogConfig);
    dialogRef.afterClosed().subscribe((res) => {
      res.data.forEach((element) => {
        this.mngForm.patchValue({
          costCenterCode: element.costCenterCode,
        });
      });
    });
  }

  selectedExpenseItem: any[] = [];
  expenseLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "800px";
    dialogConfig.data = {
      action: "View Expense",
      selected: this.selectedExpenseItem,
    };
    const dialogRef = this.dialog.open(ExpenseLookupComponent, dialogConfig);
    dialogRef.afterClosed().subscribe((res) => {
      res.data.forEach((element) => {
        this.mngForm.patchValue({
          expenseCode: element.expenseCode,
          expenseName: element.expenseName,
        });
      });
    });
  }

