  // ****************************************************************************************************************************
  // submit button function

  posting: boolean = false;

  submit() {
    this.posting = true;

    let upload = this.mngForm.value;
    upload.monthlyDistributions = this.dataSource.data;

    console.log(upload);

    if (this.pageFunction === "Add") {
      this.budgetManService
        .addBudget(upload)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            console.log("RESPONSE:: => ", res);

            if (res.statusCode === 201) {
              this.snackbar.showNotification("snackbar-success", res.message);
              this.router.navigate(["/erp-budget/budget/all-budgets"]);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.posting = false;
          },
        }),
        Subscription;
    } else if (this.pageFunction === "Update") {
      this.budgetManService
        .updateBudgets(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            if (res.statusCode == 201 || 200 || 302) {
              this.snackbar.showNotification("snackbar-success", res.message);
              this.router.navigate(["/erp-budget/budget/all-budgets"]);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.posting = false;
          },
        }),
        Subscription;
    }
  }

  //Uploading a batch of items
  submitBatch() {
    this.posting = true;
    if (this.pageFunction === "Add" && this.selectedFile) {
      let params = { sheetIndex: 4 };
      const formData = new FormData();
      formData.append("file", this.selectedFile);

      this.budgetManService
        .extractBulkBudgets(params, formData)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            console.log("resresres::: ", res);
            if (
              res.statusCode == 201 ||
              res.statusCode == 200 ||
              res.statusCode == 302
            ) {
              this.snackbar.showNotification("snackbar-success", res.message);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.posting = false;
          },
        });
    }
  }

  selectedFile: File;
  onFileSelected(event): void {
    this.selectedFile = event.target.files[0];
  }
