import { DatePipe } from '@angular/common';
import { HttpParams } from '@angular/common/http';
import { Component, Inject, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { Subject, takeUntil, Subscription } from 'rxjs';
import { BudgetReportsService } from 'src/app/erp-budget/data/services/budget-reports.service';
import { GlsLookupComponent } from 'src/app/erp-finance/data/lookups/gls-lookup/gls-lookup.component';
import { PurchaseOrderAcknowledgementComponent } from 'src/app/erp-procurement/modules/requisition/pages/purchase-order-acknowledgement/purchase-order-acknowledgement.component';
import { FilesService } from 'src/app/shared/services/files.service';
import { SnackbarService } from 'src/app/shared/services/snackbar.service';

@Component({
  selector: 'app-budget-report-dialog',
  templateUrl: './budget-report-dialog.component.html',
  styleUrls: ['./budget-report-dialog.component.sass']
})
export class BudgetReportDialogComponent implements OnInit {

  mngForm: FormGroup;
  hide3 = true;
  agree3 = false;

  destroy$: Subject<boolean> = new Subject<boolean>();

  downloadposting: boolean = false;

  pageFunction: string = "Add";

  hideSubmit: boolean = false;

  posting: boolean = false;

  types = [{ name: "PDF" }];

  // , { name: "EXCEL" }
  months: any[] = [
    { value: 1, viewValue: 'January' },
    { value: 2, viewValue: 'February' },
    { value: 3, viewValue: 'March' },
    { value: 4, viewValue: 'April' },
    { value: 5, viewValue: 'May' },
    { value: 6, viewValue: 'June' },
    { value: 7, viewValue: 'July' },
    { value: 8, viewValue: 'August' },
    { value: 9, viewValue: 'September' },
    { value: 10, viewValue: 'October' },
    { value: 11, viewValue: 'November' },
    { value: 12, viewValue: 'December' }
  ];

  constructor(
    public dialogRef: MatDialogRef<PurchaseOrderAcknowledgementComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    private budgetReportsService: BudgetReportsService,
    private snackbar: SnackbarService,

  ) { }

  ngOnInit(): void {
    this.mngForm = this.createReportForm();
  }

  createReportForm(): FormGroup {
    return this.fb.group({
      type: ["EXCEL", Validators.required],
      month: [""],
      year: [""],
    });
  }

  ngAfterViewInit() { }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  // **********************************************************************************************************************************************************

  response: any;
  subscription: Subscription
  submit() {
    console.log("this.mngForm.value: ", this.mngForm.value);

    this.posting = true;

    let params = {
      year: this.mngForm.value.year,
      month: this.mngForm.value.month
    }
    if (this.data.type === "BudgetVariance") {
      let type = this.mngForm.value.type;
      if (type == "EXCEL") {
        this.subscription = this.budgetReportsService
          .downloadVarianceExcelReport(params)
          .subscribe(
            (response) => {

              const a = document.createElement('a');
              document.body.appendChild(a);
              const blob: any = new Blob([response.data], { type: 'octet/stream' });
              const url = window.URL.createObjectURL(blob);
              a.href = url;
              a.download = "BudgetVariance.xlsx"
              a.click();
              window.URL.revokeObjectURL(url);

              this.posting = false;

              this.dialogRef.close();

              this.snackbar.showNotification(
                "snackbar-success",
                "Report generated successfully"
              );
            },
            (err) => {
              this.posting = false;

              this.dialogRef.close();

              this.snackbar.showNotification(
                "snackbar-danger",
                err.message
              );
            }
          );
      }
    } else if (this.data.type === "BudgetBalance") {
      let type = this.mngForm.value.type;
      if (type == "EXCEL") {
        this.subscription = this.budgetReportsService
          .downloadBudgetBalanceReport(params)
          .subscribe(
            (response) => {

              const a = document.createElement('a');
              document.body.appendChild(a);
              const blob: any = new Blob([response.data], { type: 'octet/stream' });
              const url = window.URL.createObjectURL(blob);
              a.href = url;
              a.download = "BudgetBalance.xlsx"
              a.click();
              window.URL.revokeObjectURL(url);

              this.posting = false;

              this.dialogRef.close();

              this.snackbar.showNotification(
                "snackbar-success",
                "Report generated successfully"
              );
            },
            (err) => {
              this.posting = false;

              this.dialogRef.close();

              this.snackbar.showNotification(
                "snackbar-danger",
                err.message
              );
            }
          );
      }
    }
  }
  onNoClick(): void {
    this.dialogRef.close({ event: "close", data: this.response });
  }
}
