import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { BudgetReportDialogComponent } from '../dialogs/budget-report-dialog/budget-report-dialog.component';

@Component({
  selector: 'app-budget-reports',
  templateUrl: './budget-reports.component.html',
  styleUrls: ['./budget-reports.component.scss']
})
export class BudgetReportsComponent implements OnInit {

  constructor(private dialog: MatDialog, private router: Router) {}

  ngOnInit(): void {}

  generateReport(type: any) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "600px";
    dialogConfig.data = {
      type: type,
    };
    const dialogRef = this.dialog.open(BudgetReportDialogComponent, dialogConfig);
    dialogRef.afterClosed().subscribe((result) => {
      // result.data.forEach((element) => {
      // });
    });
  }

  previewReport(type: any) {
    if (type === "ProfitAndLoss") {
      let route = "/erp-finance/finance-management/preview-profit-loss";
      this.router.navigate([route], {
        queryParams: {
          action: "View",
        },
      });
    } else if (type === "BalanceSheet") {
      let route = "/erp-finance/finance-management/preview-balance-sheet";
      this.router.navigate([route], {
        queryParams: {
          action: "View",
        },
      });
    }else if (type === "TrialBalance") {
      let route = "/erp-finance/finance-management/preview-trial-balance";
      this.router.navigate([route], {
        queryParams: {
          action: "View",
        },
      });
    }
  }
}
