import { Component, Inject, OnInit } from "@angular/core";
import { BudgetComponent } from "../budget/budget.component";
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialogConfig,
  MatDialog,
} from "@angular/material/dialog";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { BudgetsService } from "src/app/erp-budget/data/services/budget.service";
import { Subject, takeUntil, Subscription } from "rxjs";
import { SnackbarService } from "src/app/shared/services/snackbar.service";
import { AccessControlService } from "src/app/erp-procurement/data/services/_AccessControlService.service";
import { ExpenseLookupComponent } from "src/app/erp-finance/data/lookups/expense-lookup/expense-lookup.component";

@Component({
  selector: "app-mng-budget-dialog",
  templateUrl: "./mng-budget-dialog.component.html",
  styleUrls: ["./mng-budget-dialog.component.sass"],
})
export class MngBudgetDialogComponent implements OnInit {
  pageFunction = "Update";
  formData: any;
  mngForm: FormGroup;
  constructor(
    public dialogRef: MatDialogRef<BudgetComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    private budgetService: BudgetsService,
    private snackbar: SnackbarService,
    private accessControlService: AccessControlService,
    private dialog: MatDialog
  ) { }

  ngOnInit(): void {
    this.formData = this.data.selected;

    this.generateForm(this.formData);
  }

  generateForm(formData?): void {
    this.mngForm = this.fb.group({
      id: [formData?.id || ""],
      // expCode: [formData?.expenseCode || "", [Validators.required]],
      // expName: [formData?.expenseName || ""],
      allowOverride: [formData?.allowOverride || ""],
    });
  }

  selectedExpenseItem: any[] = [];
  expenseLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "800px";
    dialogConfig.data = {
      action: "Single Expense",
      selected: this.selectedExpenseItem,
    };
    const dialogRef = this.dialog.open(ExpenseLookupComponent, dialogConfig);
    dialogRef.afterClosed().subscribe((res) => {
      res.data.forEach((element) => {
        this.mngForm.patchValue({
          expCode: element.expenseCode,
          expName: element.expenseName,
        });
      });
    });
  }
  budgetOverride(event: any) {
    if (event.value == "Yes") {
    }
  }
  hasOverridePrivilege: boolean = true;
  hasAccess: boolean;

  checkBudgetOverridePrivilege() {
    let privilege = "Manage Budget Override";

    this.hasAccess = this.accessControlService.hasPrivilege("BudgetModule", [
      privilege,
    ]);

    if (this.hasAccess) {
      this.hasOverridePrivilege = true;
    } else {
      this.hasOverridePrivilege = false;
    }
  }
  isLoading: boolean = false;
  posting: boolean = false;
  destroy$: Subject<boolean> = new Subject<boolean>();
  response: any;
  submit() {
    this.posting = true;
    if (this.pageFunction === "Update") {

      console.log("this.mngForm.value :: ", this.mngForm.value);


      this.budgetService
        .updateBudgets(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            this.response = res;
            console.log("resres:: ", res);
            if (res.statusCode == 201 || 200 || 302) {
              this.snackbar.showNotification("snackbar-success", res.message);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.posting = false;
            this.onNoClick();
          },
        }),
        Subscription;
    }
  }
  onNoClick(): void {
    this.dialogRef.close({ event: "close", data: this.response });
  }
}
