import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { environment } from "src/environments/environment.prod";

@Injectable({
  providedIn: "root",
})
export class ExpenseManagementService {
  constructor(private http: HttpClient) {}

  // ****************************************************************************************************************************

  getExpenses(): Observable<any> {
    const expenseUrl = `${environment.baseUrlFinance}/api/v1/expenses/all`;
    return this.http.get<any[]>(expenseUrl);
  }

  addExpense(data: any): Observable<any> {
    const expenseUrl = `${environment.baseUrlFinance}/api/v1/expenses/create`;
    return this.http.post<any[]>(expenseUrl, data);
  }

  updateExpense(data: any): Observable<any> {
    const expenseUrl = `${environment.baseUrlFinance}/api/v1/expenses/update`;
    return this.http.put<any[]>(expenseUrl, data);
  }

  deleteExpenseTemporarily(params: any): Observable<any> {
    const expenseUrl = `${environment.baseUrlFinance}/api/v1/expenses/delete`;
    return this.http.delete<any[]>(expenseUrl, {params});
  }

  uploadBatchExpenses(data: any): Observable<any> {
    const expenseUrl = `${environment.baseUrlFinance}/api/v1/expenses/create/bulk`;
    return this.http.post<any>(expenseUrl, data);
  }

  getCostCentersByExpenseCode(expenseGlCode: any): Observable<any> {
    const expenseUrl = `${environment.baseUrlFinance}/api/expenses/allCostCenters?expenseCode=${expenseGlCode}`;
    return this.http.get<any>(expenseUrl, expenseGlCode);
  }

  getExpensesByStatus(params): Observable<any> {
    const expenseUrl = `${environment.baseUrlFinance}/api/v1/expenses/fetchbyStatus`;
    return this.http.get<any[]>(expenseUrl, { params });
  }
  getExpenseById(params: any): Observable<any> {
    return this.http.get<any>(
      `${environment.baseUrlFinance}/api/v1/expenses/find/by/id`,
      { params }
    );
  }

  validateBulkExpense(processedRows: any): Observable<any> {
    const expenseUrl = `${environment.baseUrlFinance}/api/v1/expenses/update/state`;
    return this.http.put(expenseUrl, processedRows);
  }
}
